          width: 150,
        },
        {
          title: "Actions",
          key: "actions",
          width: 170,
        },
      ],
      dataAE: listAE,
      initialData: listAE,
    };
  },
  watch: {
    data_filter: {
      handler(val) {
        this.dataAE = this.initialData.filter(
          (item) =>
            // (!val.year || new Date(item.delai).getFullYear() === val.year) &&
            (!val.year ||
              Number(new Date(item.delai).getFullYear()) == val.year) &&
            (!val.service || item.service.id === val.service) &&
            (!val.zone || item.zone.id === val.zone) &&
            (!val.poste_travail ||
              item.poste_travail.id === val.poste_travail) &&
            (!val.activite || item.activite.id === val.activite) &&
            (!val.impact || item.impact.id === val.impact) &&
            (!val.aspect || item.aspect.id === val.aspect) &&
            (!val.theme || item.theme.id === val.theme) &&
            (!val.situation || item.situation.id === val.situation) &&
            (!val.F_G || item.F_G <= val.F_G[1]) &&
            (!val.F_G || item.F_G >= val.F_G[0]) &&
            (!val.impact_residuel ||
              item.impact_residuel <= val.impact_residuel[1]) &&
            (!val.ir || item.impact_residuel >= val.impact_residuel[0])
        );
      },
      deep: true,
    },
  },
  computed: {
    visibleHeaders(): Array<any> {
      return this.headerAE.filter(
        (header) => header.expand !== this.isExpanded
      );
    },
    currentRouteName() {
      return window.location.origin;
    },
    pageCount(): number {
      return Math.ceil(this.dataAE.length / this.itemsPerPage);
    },
    // compute the current page's items based on the current page and items per page
    pageItems(): Array<IAE> {
      const startIndex = (this.page - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return this.dataAE.slice(startIndex, endIndex);
    },
  },
  setup() {
    const width_m_m_t = ref(300);
    onMounted(() => {
      setCurrentPageBreadcrumbs("AEs Listing", ["Analyse environnementale"]);
    });
  },
  mounted() {
    this.fetch();
  },
  methods: {
    exportToExcel() {
      jsonToExcel(this.dataAE, "Analyse Environnementale");
    },
    async fetch() {
      const analyse = await axios.get(this.currentRouteName + "/api/analyse");
      this.dataAE = analyse.data;
      this.initialData = analyse.data;
    },
    toggleColumn() {
      this.isExpanded = !this.isExpanded;
      this.icon_expand = this.isExpanded ? "expand" : "collapse";
    },
    setpage(pageNumber) {
      this.page = pageNumber;
    },
    previousPage() {
      if (this.page > 1) {
        this.page--;
      }
    },
    nextPage() {
      if (this.page < this.pageCount) {
        this.page++;
      }
    },
    editItem(item) {
      console.log("Edit Item: ", item);
    },
    async deleteItem(item) {
      const id = item.raw.id;
      await axios
        .delete(this.currentRouteName + "/api/analyse/" + id)
        .then((response) => {
          Swal.fire({
            text: "Analyse environnementale bien Supprim",
            icon: "success",
            buttonsStyling: false,
            confirmButtonText: "Ok",
            customClass: {
              confirmButton: "btn fw-bold btn-light-danger",
            },
          });
        })
        .catch(function (error) {
          Swal.fire({
            text: error,
            icon: "error",
            buttonsStyling: false,
            confirmButtonText: "Ressayer!",
            customClass: {
              confirmButton: "btn fw-bold btn-light-danger",
            },
          });
        });
      const indexToRemove = this.dataAE.findIndex((item) => item.id === id);
      if (indexToRemove !== -1) {
        this.dataAE.splice(indexToRemove, 1);
      }
    },
    setRowStyle(item) {
      return "style-1";
    },
    changeWidth(value) {
      const tr = value.target.parentNode;
      if (tr.classList.contains("max-width-cell")) {
        console.log(tr);
        tr.classList.remove("max-width-cell");
      } else {
        console.log(tr);
        tr.classList.add("max-width-cell");
      }
    },
    mouseDownHandler(e) {
      console.log(e);
    },
  },
});
